import{createBirpc}from'birpc';export const setupServerConnection=async options=>{const{url,$store}=options;const ws=new window.WebSocket(url);const server=createBirpc({refresh:()=>location.reload(),updateFileSystemRoutes({entrypoint,routes}){$store.framework.fileSystemRoutes[entrypoint.entryName]=routes;}},{post:data=>ws.send(data),on:cb=>ws.onmessage=cb,serialize:v=>JSON.stringify(v),deserialize:v=>JSON.parse(v.data.toString())});await new Promise((resolve,reject)=>{ws.onopen=()=>resolve();ws.onerror=()=>reject(new Error(`Failed connect to WebSocket server: ${url}`));});return{server};};