import _ from'lodash';import{useMemo}from'react';import{proxy,useSnapshot}from'valtio';export const useProxyFrom=initializer=>{return useMemo(()=>proxy(initializer()),[]);};export const useStoreMap=(arr,keyProp)=>{const prop=keyProp??'key';const pairs=useSnapshot(arr).map(i=>{const key=i[prop];if(_.isString(key)||_.isNumber(key)){return[key,proxy(i)];}else{throw new TypeError(`Can't found correct key field.`);}});return pairs;};