import React from'react';import styled from'@emotion/styled';import{Button,Flex}from'@radix-ui/themes';import{ChevronRightIcon}from'@radix-ui/react-icons';export const BreadcrumbButton=props=>{return<Button color="gray"size="1"variant="ghost"{...props}/>;};export const Breadcrumbs=({children=[]})=>{const elements=[];for(const child of children){elements.push(child);if(child!==children.at(-1)){elements.push(<Connector key={`${child.key}__connector`}/>);}}return<Flex align="center"gap="1">
      {elements}
    </Flex>;};Breadcrumbs.Button=BreadcrumbButton;export default Breadcrumbs;const Connector=styled(ChevronRightIcon)({width:'var(--font-size-2)',height:'var(--font-size-2)',color:'var(--gray-6)'});